class API {
	constructor() {
		this.api_url = 'https://api.radio.funny-dev.ru/';
	}

	_buildQuery = ( data ) => {
		var values = [];

		Object.keys( data ).forEach( ( key ) => {
			values.push( key + '=' + encodeURIComponent( data[key] ) );
		} );

		return values.join( '&' );
	}

	_request = ( method, data, success_callback, error_callback ) => {
		var xhr = new XMLHttpRequest();
		var url = this.api_url + method + '?' + this._buildQuery( data );

		xhr.open( 'GET', url, true );
		xhr.send();

		xhr.onreadystatechange = () => {
			if ( xhr.readyState === 4 && xhr.status >= 200 && xhr.status < 300 ) {
				var res = xhr.responseText;

				try {
					var response = JSON.parse( res );
					success_callback( response );
				} catch ( e ) {
					if ( typeof error_callback === 'function' ) error_callback( e );
				}
			}
		}

		xhr.onerror = ( event ) => {
			if ( typeof error_callback === 'function' ) error_callback( event );
		}
	}

	getPlaylist = ( station_id, success_callback, error_callback ) => {
		return this._request( 'playlists/playlist_' + station_id, {}, success_callback, error_callback );
	}

	getCurrent = ( station_id, success_callback, error_callback ) => {
		return this._request( 'playlists/current_' + station_id, {}, success_callback, error_callback );
	}
}


export default API;