import React from 'react';
import './ScrollText.css';


class ScrollText extends React.Component {
	constructor( props ) {
		super( props );

		this.state = {
			scrollable: false
		};
	}

	componentDidMount() {
		//this.toggle();
	}

	componentDidUpdate( prevProps ) {
		if ( prevProps.children !== this.props.children ) {
			//this.toggle();
		}
	}

	toggle = () => {
		var container = document.getElementById( 'scrolltext-container' );
		var content = document.getElementById( 'scrolltext' );

		if ( container.offsetWidth < content.offsetWidth && !this.state.scrollable ) {
			this.setState( { scrollable: true } );
		} else {
			this.setState( { scrollable: false } );
		}
	}

	render() {
		return (
			<div className="c-scroll-text" id="scrolltext-container">
				<div className={"c-scroll-text__content" + ( this.state.scrollable ? ' c-scroll-text__content--scrollable' : '' ) }
					 id="scrolltext">
					 	{this.props.children}
				</div>
			</div>
		);
	}
};


export default ScrollText;