import React from 'react';
import { Panel } from '@vkontakte/vkui';
import { ModalRoot, Modal, Category, PrimaryButton, H, Switch, Select, InputNote, EmptyFiller, Header, Content } from './../components/index.js';
import ExternalLink from './../ExternalLink.js'

import API from './../service/API.js';


class Playlist extends React.Component {
	constructor( props ) {
		super( props );
			this.state = {
			playlist: [],
			stationId: this.props.stationId
		};


		this.api = new API();
	}

	componentDidMount() {
		this.load();
	}

	componentWillUnmount() {
		
	}

	componentDidUpdate( prevProps, prevState ) {
		if ( prevState.stationId !== this.state.stationId ) {
			this.load();
		}
	}

	load = () => {
		this.api.getPlaylist( this.state.stationId, ( res ) => {
			this.setState( { playlist: res.reverse() } );
		} );
	}

	closeModal = () => {
		this.props.onClose();
	}

	prepareTime = ( unixtime ) => {
		var date = new Date( unixtime * 1000 );
		var h = date.getHours();
		var m = date.getMinutes();

		h = h < 10 ? '0' + h : h;
		m = m < 10 ? '0' + m : m;

		return h + ' ' + m;
	}

	searchInVK = ( e ) => {
		var hash = e.currentTarget.dataset.key;
		var obj = {};

		this.state.playlist.forEach( ( item ) => {
			if ( item.hash === hash ) obj = item;
		} );

		if ( !obj.hash ) {
			return;
		}

		let search_query = obj.artist + ' ' + obj.song;
		let link = 'https://vk.com/audio?q=' + search_query;
		ExternalLink.open( link );
	}

	

	render() {
		return (
			<Panel id={this.props.id}>
				<Header icon="browser_back" onClick={this.props.goBack}></Header>
				<Content>
					{this.state.playlist.map( ( item, i ) => {
						var date = this.prepareTime( item.time );
						return (
							<Category title={item.song}
								subtitle={item.artist}
								left={date}
								iconColor="brown"
								nopad="true"
								leftType="text"
								onClick={this.searchInVK}
								data-key={item.hash}
								key={item.hash}
							 />
						);
					} )}
				</Content>
			</Panel>
		);
	}
};


export default Playlist;
