import React from 'react';
import './Banner.css';

import ExternalLink from '../../ExternalLink.js';

export default class Banner extends React.Component {
	constructor( props ) {
		super( props );

		this.banner = props.banner;
	}

	click = () => {
		if ( this.banner.type == 'link' ) {
			ExternalLink.open( this.banner.link );
		} else if ( this.banner.type == 'screen' && typeof this.props.navigate == 'function' ) {
			this.props.navigate( this.banner.link );
		}
	}

	render() {
		return (
			<div className="c-gallery__card" style={{background: 'url(' + this.banner.image + ' )'}} onClick={this.click}>
				<div className="c-gallery__type-text">{this.banner.category}</div>
				<div className="c-gallery__main-content">
					<div className="c-gallery__title">{this.banner.title}</div>
					<div className="c-gallery__subtitle">{this.banner.text}</div>
				</div>
			</div>
		);
	}
}